.settings-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.settings-contant {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow-y: auto;
  max-width: 700px;
  width: 100%;
  height: 100%;
}

.settings-page {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px 15px;
}

.item-wrapper {
  background: var(--secondary-color);
  height: auto;
  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-radius: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  color: var(--primary-color);

  @media (max-width: 500px) {
    flex-direction: column;
    align-items: flex-start;
  }

  &:hover {
    .rotate-animation {
      transform: rotate(180deg);
      animation-timing-function: cubic-bezier(.25,-0.73,.66,.86);
      transition: 0.5s;
    }

    .jump-animation {
      animation: jumpAnimation 0.4s cubic-bezier(.25,-0.73,.66,.86) 2;
    }
  }

  p {
    padding-left: 10px;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
    margin: 0;
    margin-right: 10px;
    color: var(--primary-color);

    @media (max-width: 500px) {
      font-size: 14px;
      line-height: 16px;
      margin-bottom: 4px;
    }
  }
}

@keyframes jumpAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0);
  }
}
