.chat__wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  overflow-y: hidden;
  position: relative;
}

.chat__messages {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.chat__top-button {
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 4;
  backdrop-filter: blur(6px);
  color: var(--text-color-active);
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 10px;
  text-decoration: none;
  
  p {
    margin: 0;
    margin-left: 8px;
  }

  @media (max-width: 500px) {
    padding: 8px 4px;

    p {
      margin-left: 4px;
    }
  }
}

.chat__messages::-webkit-scrollbar {
  width: 0;
}

::-webkit-scrollbar {
  width: 0;
}
