.navabar-bottom__link {
  text-decoration: none;
  display: flex;
  color: var(--primary-color);
  margin-right: 20px;
  align-items: center;
  padding: 6px 10px;
  border-radius: 4px;
  transition: 0.2s;

  &:last-child {
    margin-right: 0;
  }

  p {
    display: none;
    font-size: 14px;
    font-weight: 700;
    margin: 0 0 0 10px;
    opacity: 0;
  }

  &:hover {
    background: var(--primary-color);
    color: var(--third-color);
    animation: jumpAnimation2 0.4s cubic-bezier(0.25, -0.73, 0.66, 0.86) 1;
  }

  &:active {
    background: var(--active-bg-color);
    color: var(--active-font-color);
  }
}

.navabar-bottom__link_active {
  background: var(--primary-color);
  color: var(--third-color);
  transition-property: background-color;
  transition-duration: 3s;
  text-decoration: none;
  display: flex;
  margin-right: 20px;
  align-items: center;
  padding: 6px 10px;
  border-radius: 4px;
  transition: 0.2s;

  &:last-child {
    margin-right: 0;
  }

  p {
    display: block;
    opacity: 1;

    font-size: 14px;
    font-weight: 700;
    margin: 0 0 0 10px;
  }
}

@keyframes jumpAnimation2 {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(4deg);
  }
  50% {
    transform: rotate(0);
  }
  75% {
    transform: rotate(4deg);
  }
  100% {
    transform: rotate(0);
  }
}

.my-node-enter {
  opacity: 0;
}
.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.my-node-exit {
  opacity: 1;
}
.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}